.menu-container {
  width: 100%;
  padding: 15px 0;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 1000; }
  .menu-container .menu {
    width: 80%;
    margin: 0 auto; }
  .menu-container p {
    display: inline;
    padding: 0 20px;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 13px;
    cursor: pointer;
    transition: .3s ease-in-out; }
    .menu-container p:hover {
      color: #E5FF3E; }
